import RestaurantCard from "./RestaurantCard";
import { restaurants } from "../utils/mockData";
import { useEffect, useState } from "react";
// import axios from "axios";

const apiURL="https://www.swiggy.com/dapi/restaurants/list/v5?lat=18.533117461252107&lng=73.82439438253641&is-seo-homepage-enabled=true&page_type=DESKTOP_WEB_LISTING";
const corsProxyUrl = 'https://cors.bridged.cc/';


const Body=()=>{
    const [listedRestaurants,setListedRestaurants]=useState(restaurants);
    useEffect(()=>{
        fetchData();
        console.log("useEffect has been called");
    },[])

    const fetchData= async ()=>{
        try {
            const data=await fetch(apiURL);
            const json=await data.json();
            console.log(json);    
        } catch (error) {
            console.error('Error fetching data:', error);
        }
        
    };


    return(
        <div className="body">
            <div className="search">
                <button className="filter-btn" onClick={()=>{
                    listFiltered=restaurants.filter(res=>res.info.avgRating > 4)
                    setListedRestaurants(listFiltered);
                    console.log("Filtered Sir !!",listFiltered);
                }}>
                    Top rated restaurants
                </button>
            <div className="go-back">
                <button onClick={()=>{
                    normalList=restaurants.filter(res=>res.info);
                    setListedRestaurants(normalList)
                }}>All restaurants</button>
            </div>
            </div>
            <div className="container">
               <div className="res-container">
               {
                listedRestaurants.map((res)=>(
                <RestaurantCard key={res.info.id} resData={res}/>
                ))
               }  
               </div>
            </div>
        </div>
    )
}

export default Body;