import logo from "../assets/restaurant-logo.png"

const Header=()=>{
    return(
        <div className="header">
            <div className="img-logo">
            <img className="logo" src={logo} alt="image" />
            </div>
            <div className="nav-items">
                 <ul>
                    <li>Home</li>
                    <li>About us</li>
                    <li>Contact us</li>
                    <li>Cart</li>
                 </ul>
            </div>
        </div>
    )
}

export default Header;